import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import FeatureETH from '@/components/featureETH'
import FeatureEOS from '@/components/featureEOS'
import SubmitDApp from '@/components/submitDApp'
import SubmitToken from '@/components/submitToken'
import test from '@/components/test'

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/:lang',
      name: 'Index',
      props: true,
      component: Index
    },
    {
      path: '/feature/ethereum',
      name: 'FeatureETH',
      props: true,
      component: FeatureETH
    },
    {
      path: '/feature/eos',
      name: 'FeatureEOS',
      props: true,
      component: FeatureEOS
    },
    {
      path: '/submit/dapp',
      name: 'SubmitDApp',
      props: true,
      component: SubmitDApp
    },
    {
      path: '/submit/token',
      name: 'SubmitToken',
      props: true,
      component: SubmitToken
    },
    {
      path: '/submit/test',
      name: 'test',
      props: true,
      component: test
    },
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



