<template>
    <div id="c_header">
      <header id="header">
        <div class="inner_l">
          <h1>
            <a href="/">
              <span>NOVA</span>
            </a>
          </h1>
          <ul id="nav">
            <li id="home"><v-btn @click="href('/')">Home</v-btn></li>
            <li id="feature"><v-btn @click="href('/feature/ethereum')">Features</v-btn></li>
            <li id="dapp"><v-btn @click="href('/feature/eos')">DApp</v-btn></li>
            <li id="submit"><v-btn @click="href('/submit/token')">Submit</v-btn></li>
          </ul>
          <div class="langBox">
            <div>
              <v-menu bottom offset-y>
                <template v-slot:activator="{ on }">
                  <v-btn
                    v-on="on"
                    @click="langToggle"
                  >
                    <span class="titleIcon"><img :src="langTitle['icon']"/></span>
                    {{langTitle['title']}}
                    <span class="langArw">
                    <v-icon v-if="langOn">arrow_drop_down</v-icon>
                    <v-icon v-else>arrow_drop_up</v-icon>
                  </span>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-tile
                    v-for="(item, index) in langList"
                    :key="index"
                    @click="$EventBus.$emit('lang_event',item.title), langChange(item.title)"
                  >
                    <span class="icon"><img :src="item.icon"/></span>
                    <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </div>
          </div>
        </div>
        <a href="javascript:void(0)" @click="drawer=true" class="mbNavBtn" v-if="!drawer">
          <v-icon>menu</v-icon>
        </a>
        <a href="javascript:void(0)" @click="drawer=false" class="mbNavBtn" v-else>
          <v-icon>close</v-icon>
        </a>
      </header>

      <v-navigation-drawer
        v-model="drawer"
        app
        id="mb_header"
      >
        <v-list>
          <v-list-tile class="logoTitle">
            <span class="logo"><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/nova_logo.png"/></span>
            <h1>NOVA</h1>
            <a href="javascript:void(0)" @click="drawer=false" class="closeBtn"><v-icon>close</v-icon></a>
          </v-list-tile>

          <template v-for="item in items">
            <v-list-group
              v-if="item.children"
              :key="item.title"
            >
              <v-list-tile slot="activator">
                <v-list-tile-content>
                  <v-list-tile-title class="depth">
                    {{ item.title }}
                  </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile
                v-for="(child, i) in item.children"
                :key="i"
                v-bind:to="child.action"
                class="subMenu"
                :id="child.id"
              >
                <v-list-tile-content>
                  <v-list-tile-title class="childMenu">
                    {{ child.title }}
                  </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list-group>
            <v-list-tile
              v-else
              :key="item.title"
              v-bind:to="item.action"
              :id="item.id"
            >
              <v-list-tile-content>
                <v-list-tile-title class="depth">
                  {{ item.title }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </template>


          <div class="langBox">
            <div>
              <v-menu bottom offset-y>
                <template v-slot:activator="{ on }">
                  <v-btn
                    v-on="on"
                    @click="langToggle"
                  >
                    <span class="titleIcon"><img :src="langTitle['icon']"/></span>
                    {{langTitle['title']}}
                    <span class="langArw">
                    <v-icon v-if="langOn">arrow_drop_down</v-icon>
                    <v-icon v-else>arrow_drop_up</v-icon>
                  </span>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-tile
                    v-for="(item, index) in langList"
                    :key="index"
                    @click="$EventBus.$emit('lang_event',item.title), langChange(item.title), drawer=!drawer"
                  >
                    <span class="icon"><img :src="item.icon"/></span>
                    <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </div>
          </div>

        </v-list>
      </v-navigation-drawer>


    </div>
</template>

<script>
  import { langPr, langTitle, langRes, langEvent } from '../../translations/langEvent'

  export default {
    name: "headerCon",
    data(){
      return{
        lang: [],
        langTitle: [],
        langList: [
          { title: '', icon:'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_ko.png' },
          { title: 'ENG', icon:'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_en.png'  },
        ],
        langOn:true,
        drawer: null,
        items: [
          {
            title: 'HOME',
            id: 'm_home',
            action: '/'
          },
          {
            title: 'FEATURES',
            children:[
              {
                title: 'Ethereum',
                id: 'm_featureETH',
                action: '/feature/ethereum'
              },
              {
                title: 'EOS',
                id: 'm_featureEOS',
                action: '/feature/eos'
              }
            ]
          },
          {
            title: 'DAPP STATION',
            children:[
              {
                title: 'Ethereum',
                id: 'm_dappETH',
                action: '/feature/ethereum'
              },
              {
                title: 'EOS',
                id: 'm_dappEOS',
                action: '/feature/eos'
              }
            ]
          }
        ]
      }
    },
    watch: {
      '$route': 'fetchData',
    },
    created(){
      // Header Fix Ctrl
      window.onscroll = () => {
        let scrollPosition = window.scrollY,
          nav = document.getElementById('header');

        if (scrollPosition > 0) nav.classList.add('fix');
        else nav.classList.remove('fix');
      };
      // window.history.pushState(null, null, '?page=' + 'aaaa');

      this.$EventBus.$on('lang_event', (tit)=>{
        if( tit === '' ){
          this.langTitle = [];
          this.langTitle['title'] = '';
          this.langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_ko.png';
        }
        else {
          this.langTitle = [];
          this.langTitle['title'] = 'ENG';
          this.langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_en.png';
        }
      });
      document.addEventListener("backbutton", this.back, false);
      this.drawer = false;
    },
    beforeDestroy () {
      document.removeEventListener("backbutton", this.back);
    },
    mounted(){
      let scrollPosition = window.scrollY,
        nav = document.getElementById('header');
      if (scrollPosition > 0) nav.classList.add('fix');
      else nav.classList.remove('fix');

      this.fetchData();
      this.langTitle = langTitle;
    },
    methods:{
      fetchData(){
        langEvent();

        let url = location.href;

        if( url.match('feature') ) {
          let btn = document.getElementById('feature');
          this.removeClass();
          btn.classList.add('on');
        }
        else if( url.match('submit') ) {
          let btn = document.getElementById('submit');
          this.removeClass();
          btn.classList.add('on');
        }
        else {
          let btn = document.getElementById('home');
          this.removeClass();
          btn.classList.add('on');
        }
      },
      removeClass(){
        let navLi = document.getElementById('nav').children,
            navLen = document.getElementById('nav').children.length;
        for (let i = 0; i < navLen; i++) {
          let currentId = navLi[i].id;
          document.getElementById(currentId).classList.remove('on');
        }
      },
      langToggle(){
        if( this.langOn ){
          this.langOn = false;
        }
        else{
          this.langOn = true;
        }
      },
      langChange(item){
        this.langOn = true;
        if( item === '' ){
          window.history.pushState(null, null, '?l=ko');
          langEvent();
          this.langTitle = '';
          this.langTitle = langTitle;
          this.test = langTitle
        }
        else {
          window.history.pushState(null, null, '?l=en');
          langEvent();
          this.langTitle = '';
          this.langTitle = langTitle;
          this.test = langTitle
        }
      },
      href(path){
        let name = path;
        let langParam = langPr;
        this.$router.push({ path:name, query:{ l:langParam } });
      },
    }
  }
</script>

<style scoped>

</style>



