import { langText } from './language'

let langPr = '',
    langRes = [],
    langTitle = [];

langEvent();
function push_ko(){
  langRes = langText['ko'];
  langPr = 'ko';
  langTitle['title'] = '';
  langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_ko.png';
  console.log( '###push_ko###', langPr, langTitle['title'] );
}
function push_en(){
  langRes = langText['en'];
  langPr = 'en';
  langTitle['title'] = 'ENG';
  langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_en.png';
  console.log( '###push_en###', langPr, langTitle['title'] );
}


function langEvent(){
  console.log('langEvent');
  let url = location.href;
  if( url.match('index_ko.html') ) {
    push_ko();
  }
  else if(url.match('index_en.html')){
    push_en();
  }
  else{
    let urlLang = location.href;
    if(urlLang.match('ko')){
      push_ko();
    }
    else if(urlLang.match('en')){
      push_en();
    }
    else{
      let userLang = navigator.language || navigator.userLanguage;
      if( userLang === 'en-US' || userLang === 'en' ){
        push_en();
      }
      else{
        push_ko();
      }
    }
  }
}

function langTitleFnc(tit){
  if( tit === '' ){
    langTitle['title'] = '';
    langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_ko.png';
  }
  else {
    langTitle['title'] = 'ENG';
    langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_en.png';
  }
}

export { langPr, langTitle, langRes, langEvent, langTitleFnc };



