              <h2 class="title_sub s24">{{lang.token_form_title_02}} <span class="require">*</span>
                <small>(64X64)</small>
              </h2>
              <div class="file">
                <input type="text" id="fileName" v-model="imgFile" readonly v-if="fileAdd"/>
                <a href="javascript:void(0)" class="clear" @click="fileClear">
                  <img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/submit/icon-x.png"/>
                </a>
                <input type="file" id="fileOpen" ref="file" @change="fileOpen"/>
                <label for="fileOpen">
                  <v-icon>add</v-icon>
                  {{lang.file_add}}</label>
              </div>
              <p class="desc" v-if="!requireImg">{{lang.require_alert}}</p>
              <div class="imgPreview" v-if="imageData.length > 0">
