              <div class="inputWrap">
                <p class="color_desc">
                  {{lang.submit_form_agree_txt}}
                </p>
                <v-radio-group v-model="agreeValue">
                  <v-radio
                    :label="lang.submit_form_agree"
                    value="agree"
                    key="agree"
                    @change="agreeState=true, valid=true"
                  ></v-radio>
                  <v-radio
                    :label="lang.submit_form_disagree"
                    value="disagree"
                    key="disagree"
                    @change="agreeState=false, valid=false"
