    <!-- Download -->
    <div class="downWrap dapp">
      <div class="inner sm2">
        <div class="txt">
          <h2 class="title_sub s40">DApp Station</h2>
          <p class="desc">
            {{lang.dappstation_desc_1}}<br/>
