    <!-- loading -->
    <div class="loadingWrap" v-if="loading">
      <v-progress-circular
        :size="120"
        :width="6"
        color="white"
        indeterminate
      ></v-progress-circular>
    </div>

  </div>
</template>

<script>

  import { langPr, langTitle, langRes, langEvent } from '../../translations/langEvent'
  import { langText } from '../../translations/language'
  import axios from 'axios'

  export default {
    name: "submitToken",

    data() {
      return {
        lang: langRes,
        valid: false,
        dapp: [],
        dappCategory: ['game', 'gambling', 'social', 'resource', 'tool', 'exchange'],
        icoCategory: ['No Plan to Fun ICO', 'Plan to Run ICO', 'Running ICO', 'Completed ICO', 'etc'],
        agreeValue: 'agree',
        agreeState: true,
        imgFile : '',
        filePath : [],
        fileAdd : false,
        imageData : '',
        requireImg: true,
        requireAlert:'',
        requireRule : [
          v => !!v || '*Mandatory Information',
        ],
        loading : false,
      }
    },
    watch: {
      '$route': 'fetchData',
    },
    mounted() {
      this.fetchData();
      this.valid = false;
    },
    created() {
      axios.get('/api/test')
        .then(res=>{
          console.log(res);
        })
        .catch(err=>{
          console.log(err);
        })


      console.log(this.$route.params.token);
      this.$EventBus.$on('lang_event', (tit) => {
        if (tit === '') {
          this.lang = [];
          this.lang = langText['ko'];
        } else {
          this.lang = [];
          this.lang = langText['en'];
        }
      });
    },
    methods: {
      fetchData() {
        langEvent();
        this.lang = langRes;
      },
      href(path) {
        let name = path;
        let langParam = langPr;
        this.$router.push({path: name, query: {l: langParam}});
      },
      fileOpen(e){
        let fileName = e.target.files[0].name;
        this.imgFile = fileName;
        this.filePath = this.$refs.file.files[0];
        this.fileAdd = true;
        this.requireImg = true;

        let input = e.target;
        if (input.files && input.files[0]) {
          let reader = new FileReader();
          reader.onload = (e) => {
            this.imageData = e.target.result;
          }
          reader.readAsDataURL(input.files[0]);
        }
      },
      fileClear(){
        this.imgFile = '';
        this.filePath = [];
        this.fileAdd = false;
        this.imageData = '';
        this.requireImg = false;
      },
      submit(){
        if ( this.$refs.form.validate() ) {
          if( this.fileAdd ){
            this.requireImg = true;
            if( this.agreeValue === 'agree' ){
              this.loading = true;
              let formData = new FormData();
              formData.append('file', this.filePath);
              formData.append('iconName', this.imgFile);
              axios.post('/api/iconUpload',formData, { headers: { 'Content-Type': 'multipart/form-data'}})
                .then(rs=>{
                  alert('ok');
                })
                .catch(err=>{
                  alert('err');
                })
            }
            else{
              console.log('disagree');

            }
            console.log(this.dapp);
          }
          else{
            this.requireImg = false;
            let iconBox = document.getElementById('iconBox').offsetTop - 150;
            window.scrollTo(0,iconBox);
          }
        }
      },
    }
  }
</script>

