<template>
    <div>
      <footer id="footer" class="inner">
        <div class="info">
          <h1><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/logo-wizard-white.png"/></h1>
          <div class="rBox">
            <div class="langBox">
              <v-menu bottom offset-y>
                <template v-slot:activator="{ on }">
                  <v-btn
                    v-on="on"
                    @click="langToggle"
                  >
                    <span class="titleIcon"><img :src="langTitle['icon']"/></span>
                    {{langTitle['title']}}
                    <span class="langArw">
                      <v-icon v-if="langOn">arrow_drop_down</v-icon>
                      <v-icon v-else>arrow_drop_up</v-icon>
                    </span>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-tile
                    v-for="(item, index) in langList"
                    :key="index"
                    @click="$EventBus.$emit('lang_event',item.title), langChange(item.title)"
                  >
                    <span class="icon"><img :src="item.icon"/></span>
                    <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </div>
            <div class="copy">
              <span class="block">Copyright  Wizard Works Inc.</span>
              <span>All rights reserved.</span>
              <a href="javascript:void(0)">{{ lang.terms }}</a>
            </div>
          </div>
        </div>
        <div class="sitemap">
          <ul>
            <li>
              <h4>NOVA Wallet</h4>
              <ul class="list">
                <li><a href="javascript:void(0)">HOME</a></li>
                <li><a href="javascript:void(0)">Ethereum</a></li>
                <li><a href="javascript:void(0)">EOS</a></li>
                <li><a href="javascript:void(0)">DApp Station</a></li>
                <li><a href="javascript:void(0)">Key Generator</a></li>
              </ul>
            </li>
            <li class="submitList">
              <h4>For Project</h4>
              <ul class="list">
                <li><a href="javascript:void(0)">Submit Token</a></li>
                <li><a href="javascript:void(0)">Submit DApp</a></li>
                <li><a href="javascript:void(0)">Promotion</a></li>
              </ul>
            </li>
            <li>
              <h4>For Developers</h4>
              <ul class="list">
                <li><a href="javascript:void(0)">iOS SDK</a></li>
                <li><a href="javascript:void(0)">Android SDK</a></li>
              </ul>
            </li>
            <li>
              <h4>Contact</h4>
              <ul class="list contact">
                <li>
                  <a href="javascript:void(0)">
                    <span><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/icon-medium.png"/></span>
                    Medium
                  </a>
                </li>
                <li>
                  <a href="javascript:void(0)">
                    <span><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/icon-telegram.png"/></span>
                    Telegram
                  </a>
                </li>
                <li>
                  <a href="javascript:void(0)">
                    <span><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/icon-github.png"/></span>
                    Github
                  </a>
                </li>
                <li>
                  <a href="javascript:void(0)">
                    <span><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/icon-twitter.png"/></span>
                    Twitter
                  </a>
                </li>
                <li>
                  <a href="javascript:void(0)">
                    <span><img src="https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/image2.0/icon-mail.png"/></span>
                    Email
                  </a>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </footer>
    </div>
</template>

<script>
  import { langPr, langTitle, langRes, langEvent } from '../../translations/langEvent'

  export default {
    name: "footerCon",
    data(){
      return{
        lang: langRes,
        langTitle: [],
        langList: [
          { title: '', icon:'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_ko.png' },
          { title: 'ENG', icon:'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_en.png'  },
        ],
        langOn:true,
        test:'',
      }
    },
    created(){
      console.log(this.lang);
      this.$EventBus.$on('lang_event', (tit)=>{
        if( tit === '' ){
          this.langTitle = [];
          this.langTitle['title'] = '';
          this.langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_ko.png';
        }
        else {
          this.langTitle = [];
          this.langTitle['title'] = 'ENG';
          this.langTitle['icon'] = 'https://s3.ap-northeast-2.amazonaws.com/eosnova.io.resource/images/img_en.png';
        }
      });
    },
    mounted(){
      this.langTitle = langTitle;
    },
    methods:{
      langToggle(){
        if( this.langOn ){
          this.langOn = false;
        }
        else{
          this.langOn = true;
        }
      },
      langChange(item){
        this.langOn = true;
        if( item === '' ){
          window.history.pushState(null, null, '?l=ko');
          langEvent();
          this.langTitle = '';
          this.langTitle = langTitle;
          this.test = langTitle
        }
        else {
          window.history.pushState(null, null, '?l=en');
          langEvent();
          this.langTitle = '';
          this.langTitle = langTitle;
          this.test = langTitle
        }
      },
      href(path){
        let name = path;
        let langParam = langPr;
        this.$router.push({ path:name, query:{ l:langParam } });
      },
    }
  }
</script>

<style scoped>

</style>



